/* nunito-sans-200 - latin */
@font-face {
  font-family: 'Nunito Sans';
  font-style: normal;
  font-weight: 200;
  src: url('/public/shared-assets/fonts/nunito-sans-v12-latin-200.eot'); /* IE9 Compat Modes */
  src: local(''),
    url('/public/shared-assets/fonts/nunito-sans-v12-latin-200.eot?#iefix')
      format('embedded-opentype'),
    /* IE6-IE8 */
      url('/public/shared-assets/fonts/nunito-sans-v12-latin-200.woff2')
      format('woff2'),
    /* Super Modern Browsers */
      url('/public/shared-assets/fonts/nunito-sans-v12-latin-200.woff')
      format('woff'),
    /* Modern Browsers */
      url('/public/shared-assets/fonts/nunito-sans-v12-latin-200.ttf')
      format('truetype'),
    /* Safari, Android, iOS */
      url('/public/shared-assets/fonts/nunito-sans-v12-latin-200.svg#NunitoSans')
      format('svg'); /* Legacy iOS */
}
